				image: http.imgUrl+'images/brand1.png'
			}, ],
			specArr: {
				title: '颜色',
				spec: [{
					spec_id: 1,
					name: '蓝色'
				}, {
					spec_id: 2,
					name: '黑色'
				}, {
					spec_id: 3,
					name: '紫色'
				}, {
					spec_id: 4,
