	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {

		},
		aa: function() {
			uni.showLoading({
				title: '加载中'
			})
			let opts = {
				url: '',
				method: 'post'
			};
			let param = {
				city_id: '370700',
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		tabChange: function(e) {
			this.current = e;
		},
		showBuyModal: function(e) {
			console.log(e)
			if (e == 1) {
				this.openBuyModalType = 1;
				this.buyModal = true;
				this.btnStyle.backgroundColor = "#E01727"

			} else if (e == 2) {
				this.openBuyModalType = 2;
				this.buyModal = true;
				this.btnStyle.backgroundColor = "#CD9657"
			}
		},
		goIndex: function() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		goShop:function(){
			uni.navigateTo({
				url:'../shop/shopInfo'
			})
		}
	}
}
